}

.benefits__item:hover {
  background-color: #00b2ff;
}
.benefits__item:hover h3 {
  color: #f2f5f7;
}
.benefits__item:hover p {
  color: #f2f5f7;
}

.benefits__item:hover .benefits__item-icon {
  fill: #f2f5f7;
}

.benefits__item-icon {
  width: 35px;
  height: 35px;
  margin-bottom: 17px;
  fill: #00b2ff;
}
.benefits__item-icon--suppliers {
  margin-left: 2px;
}
.benefits__item-icon--confidentiality {
  margin-left: -5px;
}

.benefits__item h3 {
  margin: 0;
  margin-bottom: 13px;
  padding: 0;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 24px;
  font-weight: 400;
  line-height: 34px;
  color: #00b2ff;
}

.benefits__item p {
  margin: 0;
  padding: 0;
